.header {
    display: flex;
    justify-content: space-between;

    &__mobile-nav-btn {
        width: 18px;
        height: 15px;
        display: none;
        flex-direction: column;
        justify-content: space-between;

        & div {
            border-bottom: 2px solid #FFFFFF;
        }
    }

    &_marg49 {
        margin-top: 49px;
        margin-bottom: 41px;
    }
    &__info {
        display: flex;
        align-items: center;
    }
    &__contacts {
        display: flex;
        flex-direction: column;
    }
    &__list {
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        color: #FFFFFF;
        cursor: default;
    }
    &__list li {
        padding: 5px 6px;
    }

    &__list li:hover{
        background: #EDEDF7;
        border-radius: 8px;
        opacity: 1;
    }

    &_separator::after {
        content: '';
        width: 6px;
        height: 6px;
        background-color: #E6E6F0;
        padding: 0px;
        border-radius: 50%;
        display: inline-flex;
        align-self: center;
        margin-left: 10px;
        margin-right: 10px;
    }

    &__logo-tablet {
        display: none;
    }

    &__text {
        font-size: 16px;
        line-height: 19px; /* убрать, чтобы все было ок */
        color: #FFFFFF;
        display: flex;
    }

    &__tel-link {
        font-size: 16px;
        line-height: 19px;
        text-align: right;
        color: #FFFFFF;
        text-decoration: none;
        display: flex;

        &:hover {
            color: #F0145A;
        }
    }

    &__link svg:hover path{
        fill: #24A3FF;
    }
}

.nav {
    display: flex;
    justify-content: flex-end;
    margin-top: 19px;
    &__list {
        display: flex;
        width: 404px;
        justify-content: space-between;
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #FFFFFF;

        & a {
            border-bottom:  2px solid transparent;
        }

        & a:hover {
            border-color:#FFFFFF;
            cursor: pointer;
        }
    }
}

.active-leanguage {
    background-color: #F0145A;
    border-radius: 8px;
}

.mobile-nav {
    background-color:#FFFFFF;
    width: 100%;
    height: 0%;
    position: absolute;
    z-index: 99;
    left: 0;
    top: 0px;
    display: block;
    flex-direction: column;
    overflow: hidden;

    &__logo-container {
        display: flex;
        align-self: center;
        padding: 4.59px 19px 15px 19px;
        width: 100%;
    }

    &__close-btn {
        width: 18px;
        height: 18px;
        color: #EC6F5D;
        border: none;
        background-color: #FFFFFF;
        cursor: pointer;
    }

    &__logo {
        margin: 0 auto;
    }

    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;

        & li {
            font-weight: 500;
            font-size: 24px;
            line-height: 28px;
            text-align: center;
            color: #24A3FF;
            background-color: #F5F5FA;
            width: 100%;
            border-top: 1px solid #FFFFFF;
            height: 54px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;

            &:hover {
                color: #F0145A;
            }
        }
    }
}

.mobile-nav-active {
    height: 100%;
    transition: height 2s;
}

.mobile-nav-hidden {
    height:0%;
    transition: height 2s;
}
